"use client";
import Link from "next/link";
import { usePathname } from "next/navigation";
import {
  Box,
  VStack,
  Flex,
  Text,
  Icon,
  Button,
} from "@chakra-ui/react";
import { FiGrid, FiUsers, FiUser, FiBriefcase, FiCheckSquare, FiClock, FiSettings } from "react-icons/fi";

const navItems = [
  { href: "/dashboard", label: "Dashboard", icon: FiGrid },
  { href: "/accounts", label: "Accounts", icon: FiUsers },
  { href: "/contacts", label: "Contacts", icon: FiUser },
  { href: "/deals", label: "Deals", icon: FiBriefcase },
  { href: "/tasks", label: "Tasks", icon: FiCheckSquare },
  { href: "/time", label: "Time", icon: FiClock },
  { href: "/admin", label: "Admin", icon: FiSettings, admin: true },
];

export default function Sidebar() {
  const pathname = usePathname();
  const isAdmin = true;
  return (
    <Box
      bg="#fff"
      borderRight="1px solid #e1e4e8"
      minH="100vh"
      w="240px"
      px={4}
      py={6}
      position="fixed"
      left={0}
      top={0}
      boxShadow="md"
      zIndex={10}
      display={{ base: "none", md: "block" }}
    >
      <Flex align="center" mb={8} gap={2} px={2}>
        <Icon viewBox="0 0 32 32" boxSize={8} color="#0052cc">
          <circle cx="16" cy="16" r="16" fill="#0052cc" />
          <text x="16" y="21" textAnchor="middle" fontSize="16" fill="#fff" fontFamily="Arial">CRM</text>
        </Icon>
        <Text fontWeight="bold" fontSize="xl" color="#0052cc">CRM</Text>
      </Flex>
      <VStack align="stretch" spacing={2}>
        {navItems.map((item) => {
          if (item.admin && !isAdmin) return null;
          const isActive = pathname.startsWith(item.href);
          return (
            <Link key={item.href} href={item.href} style={{ textDecoration: 'none' }}>
              <Button
                leftIcon={<item.icon size={20} />}
                variant={isActive ? "solid" : "ghost"}
                colorScheme={isActive ? "blue" : undefined}
                fontWeight={isActive ? "bold" : "normal"}
                justifyContent="flex-start"
                w="full"
                size="lg"
                borderRadius="md"
                bg={isActive ? "#0052cc" : undefined}
                color={isActive ? "#fff" : "#222e3a"}
                _hover={{ bg: isActive ? "#0747a6" : "gray.100" }}
                boxShadow={isActive ? "md" : undefined}
                mb={1}
              >
                {item.label}
              </Button>
            </Link>
          );
        })}
      </VStack>
    </Box>
  );
}
